












import Vue from 'vue'
import { SkillUsableChecker } from '../../models/skills/SkillEnums';

interface IChecker {
    type: SkillUsableChecker;
    params: string[];
    desc?: string;
}

function op2Str(op: string): string {
    switch (op) {
        case "<": return "";
        case "":
        case "==": return "";
        case "!=": return "";
        case ">": return "";
        case "<=": return "";
        case ">=": return "";
        case "&": return "";
        case "!&": return "";
        default: return op;
    }
}

function op2Str_action(op: string): string {
    switch (op) {
        case "Air": return "";
        case "Down": return "";
        case "Hit": return "";
        case "Attack": return "";
        case "Stun": return "";
        default: return op;
    }
}

const checkerMap: {[k: number /* SKillUsableChecker */]: (params: string[]) => string|Promise<string>} = {
    [SkillUsableChecker.CASTER_STATE]: (params) => `${op2Str(params[1])} '${op2Str_action(params[0])}'`,
    [SkillUsableChecker.HAS_HP]: (params) => ` ${op2Str(params[1])} ${params[0]}%`,
    [SkillUsableChecker.PROBABILITY]: (params) => ` ${params[0]}%`,
    [SkillUsableChecker.TARGET_STATE]: (params) => ` ${params[2]}  ${op2Str(params[1])} '${op2Str_action(params[0])}' `,
    [SkillUsableChecker.CAN_MOVE]: () => ``,
    [SkillUsableChecker.CAN_JUMP]: () => ``,
    [SkillUsableChecker.IS_HIT]: () => ``,
    [SkillUsableChecker.TOGGLE_IS_ACTIVE]: async (params): Promise<string> => ` ${params[0]} `, // todo
    [SkillUsableChecker.ENERGY_CHARGE]: (params) => ` ${params}`,
    [SkillUsableChecker.CAN_GROUND_MOVE]: () => ``,
    [SkillUsableChecker.TARGET_IN_RANGE]: (params) => ` ${params} `,
    [SkillUsableChecker.HAS_BUBBLES]: async (params): Promise<string> => `${params[1]}  ID ${params[0]}`, // todo
    [SkillUsableChecker.SUMMON_IN_RANGE]: (params) => ` ${params} `,
    [SkillUsableChecker.TARGET_STATE_EFFECT]: async (params) => ` ${params} `,
};

interface IData {
    checkers: IChecker[];
}

export default Vue.extend({
    props: {
        usableCheckers: {
            type: Array as () => SkillUsableChecker[],
        },
        canUseParams: {
            type: Array as () => string[],
        },
    },
    data(): IData {
        return {
            checkers: [],
        };
    },
    watch: {
        async usableCheckers() {
            await this.evaluate();
        },
        async canUseParams() {
            await this.evaluate();
        },
    },
    async mounted() {
        await this.evaluate();
    },
    methods: {
        async evaluate() {
            this.checkers = await this.calcCheckers();  
        },
        async calcCheckers(): Promise<IChecker[]> {
            let ret: IChecker[] = [];
            let paramIdx = 0;
            for (let i = 0; i < this.usableCheckers.length; ++i) {
                const checker = this.usableCheckers[i];
                switch (checker) {
                    case SkillUsableChecker.CAN_MOVE:
                    case SkillUsableChecker.CAN_JUMP:
                    case SkillUsableChecker.IS_HIT:
                    case SkillUsableChecker.ENERGY_CHARGE:
                    case SkillUsableChecker.CAN_GROUND_MOVE:
                    case SkillUsableChecker.TARGET_IN_RANGE:
                        ret.push({
                            type: checker,
                            params: [],
                        });
                        break;
                    case SkillUsableChecker.PROBABILITY:
                    case SkillUsableChecker.TOGGLE_IS_ACTIVE:
                    case SkillUsableChecker.SUMMON_IN_RANGE:
                        ret.push({
                            type: checker,
                            params: [this.canUseParams[paramIdx++]],
                        });
                        break;
                    case SkillUsableChecker.CASTER_STATE:
                    case SkillUsableChecker.HAS_HP:
                    case SkillUsableChecker.HAS_BUBBLES:
                    case SkillUsableChecker.TARGET_STATE_EFFECT:
                        ret.push({
                            type: checker,
                            params: [this.canUseParams[paramIdx++], this.canUseParams[paramIdx++]],
                        });
                        break;
                    case SkillUsableChecker.TARGET_STATE:
                        ret.push({
                            type: checker,
                            params: [this.canUseParams[paramIdx++], this.canUseParams[paramIdx++], this.canUseParams[paramIdx++]],
                        });
                        break;
                    case SkillUsableChecker.NULL:
                    default:
                        continue;
                }
            }

            for (let v of ret) {
                const res = (checkerMap[v.type] && checkerMap[v.type](v.params)) || '';
                let resStr = '';
                if (typeof res === 'string') {
                    resStr = res;
                } else {
                    resStr = await (res as Promise<string>);
                }

                v.desc = resStr;
                v.params = v.params.filter(p => p != null);
            }

            // Filter checkers that have all undefined params
            ret = ret.filter((v) => v.params.length && 
                v.params.reduce((prev, cur) => cur !== undefined ? cur : prev, undefined as any) != undefined);

            return ret;
        }
    }
})
